import React, { Component } from 'react';

class timebar extends Component {
	handleTimerToggle = () => {
		if (this.props.timerIsPaused) {
			this.props.startTimer();
		} else {
			this.props.stopTimer();
		}
	};

	render() {
		return (
			<div>
				<span>Time Remaining: {this.props.timer}s</span>
				<span>
					{/* inline if statement to determine what icon to show */}
					<button onClick={this.handleTimerToggle}>Toggle</button>
				</span>
			</div>
		);
	}
}

export default timebar;
